@font-face {
  font-family: 'Geomanist';
  src: url("../assets/fonts/geomanist-regular-webfont.eot"), url("../assets/fonts/geomanist-regular-webfont.woff") format("woff"), url("../assets/fonts/geomanist-regular-webfont.woff2") format("woff2"), url("../assets/fonts/geomanist-regular-webfont.ttf") format("truetype"); }

h1 {
  text-align: center;
  color: #ffffff;
  font-size: 45px;
  font-weight: normal; }

html {
  overflow: hidden;
  height: 100%;
  background: url("../assets/images/background.jpg") no-repeat center center fixed;
  background-size: cover;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

body {
  font-family: Geomanist;
  height: 100%;
  overflow: auto; }



